@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap&subset=cyrillic);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:after, *:before {
  box-sizing: border-box; }

html {
  font-size: 13px; }

.button, .input, .textarea {
  border: none;
  outline: none;
  font-family: inherit;
  line-height: inherit; }

.button {
  cursor: pointer; }
  .button:disabled {
    cursor: not-allowed; }

.box {
  padding: 0.6rem 1rem;
  background-color: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(9, 45, 66, 0.25);
  border-radius: 0.25rem; }

.row, .column {
  display: flex; }

.row {
  flex-direction: row; }

.column {
  flex-direction: column;
  min-height: 0; }

.app {
  align-items: flex-start;
  height: 100vh;
  padding: 1.5rem;
  overflow-x: auto;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  line-height: 1.5rem;
  background-image: url(/react-kanban/static/media/background.0225b339.jpg);
  background-size: cover;
  background-position: center; }

.lane {
  flex: 1 1;
  max-width: 25%;
  margin-right: 1rem;
  border-radius: 0.25rem;
  background-color: #DFE3E6;
  max-height: 100%; }
  .lane:last-of-type {
    margin-right: 0; }

.lane__header {
  padding: 0.6rem 1rem 0 1rem;
  margin-bottom: 0.5rem;
  word-wrap: break-word;
  font-weight: bold;
  color: #000000; }

.lane__body {
  padding: 0 1rem;
  overflow-y: auto;
  color: #000000; }

.card {
  margin-bottom: 0.6rem;
  word-wrap: break-word;
  white-space: pre-line; }
  .card:first-of-type {
    margin-top: 0.5rem; }

.card_dragging {
  background-color: #C9D2D9;
  color: #C9D2D9;
  box-shadow: none; }

.add-new {
  padding: 1rem;
  border-radius: 0.25rem;
  color: #6B808C;
  background-color: #DFE3E6; }

.add-new_lane {
  flex: 1 1;
  max-width: 25%; }

.add-new_card {
  padding-top: 0; }

.add-new__placeholder-card {
  margin-top: 0.4rem; }

.add-new__input, .add-new__textarea {
  margin-bottom: 0.6rem; }
  .add-new__input::-webkit-input-placeholder, .add-new__textarea::-webkit-input-placeholder {
    color: #B8B9BB; }
  .add-new__input::-moz-placeholder, .add-new__textarea::-moz-placeholder {
    color: #B8B9BB; }
  .add-new__input:-ms-input-placeholder, .add-new__textarea:-ms-input-placeholder {
    color: #B8B9BB; }
  .add-new__input::-ms-input-placeholder, .add-new__textarea::-ms-input-placeholder {
    color: #B8B9BB; }
  .add-new__input::placeholder, .add-new__textarea::placeholder {
    color: #B8B9BB; }

.add-new__textarea {
  resize: none; }

.add-new__buttons {
  justify-content: space-between; }

.add-new__icon-add {
  margin-right: 0.6rem; }

.add-new__button {
  padding: 0.5rem 0.6rem;
  border-radius: 0.25rem;
  background-color: #39C071;
  font-weight: bold;
  color: #FFFFFF; }

